/* ====== VIEWPORTS ====== */
@font-face {
  font-family: "pt sans bold";
  font-weight: normal;
  font-style: bold;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
@font-face {
  font-family: "pt sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
img {
  width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
html {
  height: 100%;
  font-weight: 400;
  font-family: "pt sans", "arial", sans-serif;
  line-height: 21px;
  font-size: 14px;
}
.page-body {
  min-height: 100%;
  margin: 0;
  color: #000000;
}
@media (min-width: 1200px) {
  .page-body {
    background-color: #e7e5e5;
  }
}
.page-body__container {
  width: 100%;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-body__container {
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .page-body__container {
    margin: 0 auto;
    max-width: 1200px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  }
}
.page-main {
  flex-grow: 1;
  width: 100%;
}
.page-main__text-container {
  padding-top: 33px;
  padding-left: 25px;
  padding-right: 28px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-main__text-container {
    padding-bottom: 39px;
    padding-top: 45px;
  }
}
@media (min-width: 1200px) {
  .page-main__text-container {
    padding-bottom: 27px;
  }
}
.page-main__title {
  font-family: "pt sans bold", "arial", sans-serif;
  line-height: 26px;
  font-size: 18px;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
}
@media (min-width: 768px) {
  .page-main__title {
    padding: 0 16%;
    font-size: 21px;
  }
}
@media (min-width: 1200px) {
  .page-main__title {
    padding: 11px 23% 1px 23%;
  }
}
.page-main__text {
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 14px;
  line-height: 26px;
  padding-top: 15px;
}
.page-header {
  position: relative;
  height: 56px;
  z-index: 50;
}
@media (min-width: 768px) {
  .page-header {
    display: flex;
    justify-content: space-between;
  }
}
.page-header__link {
  display: flex;
  justify-content: center;
}
.page-header__link:active {
  opacity: 0.8;
}
.page-header__link:focus {
  opacity: 0.6;
}
.page-header__link:hover {
  opacity: 0.6;
}
.page-header__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .page-header__link {
    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    z-index: 10;
  }
}
.page-header__logo {
  z-index: 2;
}
.page-header__logo:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 187px;
    height: 84px;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    height: 97px;
  }
}
.main-nav {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-nav {
    position: relative;
    display: flex;
  }
}
.main-nav__container {
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav__container {
    display: flex;
    justify-content: center;
  }
}
@media (min-width: 1200px) {
  .main-nav__container {
    margin: 0 auto;
    max-width: 1200px;
  }
}
.main-nav--opened {
  display: block;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}
.main-nav__toggle {
  position: absolute;
  right: 0;
  top: 0;
}
.main-nav__toggle:focus-visible {
  outline: none;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs {
    display: none;
  }
}
@media (max-width: 767px) {
  .no-js {
    position: static;
    height: auto;
    background-color: #81b3d2;
  }
}
@media (max-width: 767px) {
  .hide {
    display: none;
  }
}
.toggle-button {
  border: 0;
  background-color: transparent;
  padding-top: 17px;
  padding-bottom: 17px;
  padding-left: 16px;
  padding-right: 19px;
  cursor: pointer;
}
.toggle-button:hover {
  opacity: 0.6;
}
.toggle-button:active {
  opacity: 0.3;
}
.toggle-button:focus {
  opacity: 0.6;
}
.toggle-button:focus-visible {
  outline: none;
  border: none;
}
.toggle-button__svg {
  fill: #81b3d2;
}
.main-nav__cross {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 3;
}
.cross-button {
  border: 0;
  background-color: transparent;
  padding-top: 17px;
  padding-bottom: 17px;
  padding-left: 16px;
  padding-right: 16px;
  cursor: pointer;
}
.cross-closed {
  display: none;
}
.cross--opened {
  display: block;
  z-index: 100;
}
.cross-button__svg:hover {
  opacity: 0.6;
}
.cross-button__svg:active {
  opacity: 0.3;
}
.intro__container {
