    background-position: center;
    background-size: cover;
    height: 518px;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .intro__container {
    background: url("../img/back-desktop.webp");
    background-color: #5496bd;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 518px;
    align-items: center;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .intro__container {
    background: url("../img/back-desktop@2x.webp");
    background-color: #5496bd;
