}
.control:focus-visible {
  opacity: 0.7;
}
.control-mark {
  position: absolute;
  left: 3px;
  width: 51px;
  height: 51px;
  background-color: #f2f2f2;
  border-radius: 50%;
  top: -14px;
}
.control-mark:hover {
  background-color: #e5e5e5;
}
.control-mark:active {
  background-color: #d5d5d5;
  opacity: 0.3;
}
.control-input[type="radio"]:checked + .control-mark::before {
  position: absolute;
  top: 14px;
  left: 14px;
  width: 21px;
  height: 21px;
  background-color: #242424;
  border-radius: 50%;
  content: "";
}
.control-input[type="radio"]:focus + .control-mark {
  background-color: #e5e5e5;
}
.control-input[type="checkbox"]:checked + .control-checkbox::before {
  position: absolute;
  top: 21px;
  left: 17px;
  content: url("../img/tick.svg");
}
.control-input[type="checkbox"]:focus + .control-checkbox {
  background-color: #e5e5e5;
}
